@import url(https://stackpath.bootstrapcdn.com/bootstrap/4.1.2/css/bootstrap.min.css);
@import url(https://fonts.googleapis.com/css?family=Just+Another+Hand&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
}

.App-header {
  background-color: white;
  min-height: 100px;
  padding: 40px;
  text-align: center;
}

nav,
.navbar,
.navbar-nav {
  /* margin-left: auto; */
  /* margin-right: auto; */
}

.nav-item {
  margin: 0 3rem;
  font-size: 3.5rem;
  font-family: "Just Another Hand", cursive;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
  text-align: center;
}

/* Responsive */

@media (max-width: 400px) {
  .navbar-brand {
    margin: 0;
  }
}

.PhotoList-item {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.PhotoList-item header h3 {
  margin: 3rem 0;
  font-size: 3.5rem;
  font-family: "Just Another Hand", cursive;
}

.PhotoList-content {
  height: 700px;
  max-width: 45%;
  margin: 3rem;
  line-height: 2.5rem;
  font-family: "Montserrat", sans-serif;
}

.PhotoList-text {
  max-height: 400px;
  overflow: hidden;
  margin: 1.5rem;
  position: relative;
}

.PhotoList-text:after {
  content: "";
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: 0;
  pointer-events: none;
  background-image: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0),
    rgba(255, 255, 255, 1) 95%
  );
  width: 100%;
  height: 8em;
}

.PhotoList-frame {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 50%;
  margin: 4rem auto;
  margin-left: 3rem;
  width: 50%;
  max-height: 600px;
}

.PhotoList-image {
  position: relative;
  background-image: url("https://res.cloudinary.com/fedejimenez/image/upload/v1587487866/background_frame.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 120%;
  height: 120%;
  top: -7%;
  left: -12%;
}

/* Responsive */

@media (max-width: 1199px) {
  .PhotoList-frame {
    width: 130%;
    height: 550px;
  }
  .PhotoList-image {
    padding-top: 4rem;
    width: 130%;
    height: 120%;
    top: -8%;
    left: -15%;
  }
}

@media (max-width: 992px) {
  .PhotoList-text {
    max-height: 200px;
    overflow: hidden;
    margin: 1.5rem;
    position: relative;
  }

  .PhotoList-frame {
    width: 350px;
    height: 410px;
    margin-top: 6rem;
    margin-left: 0;
  }
  .PhotoList-image {
    padding-top: 4rem;
    width: 140%;
    height: 130%;
    top: -12%;
    left: -20%;
  }
}

@media (max-width: 800px) {
  .PhotoList-item {
    width: 100%;
    display: flex;
    flex-direction: column !important;
    justify-content: space-between;
  }
  .PhotoList-content {
    height: 600px;
    max-width: 100%;
  }
  .PhotoList-frame {
    width: 350px;
    height: 500px;
    margin-top: -5rem;
    margin-bottom: 6rem;
    margin-left: auto;
  }
  .PhotoList-image {
    padding-top: 4rem;
    width: 140%;
    height: 130%;
    top: -12%;
    left: -20%;
  }
}

@media (max-width: 492px) {
  .PhotoList-frame {
    width: 220px;
    height: 320px;
    margin-top: -5rem;
    margin-bottom: 6rem;
    margin-left: auto;
  }
  .PhotoList-image {
    padding-top: 4rem;
    width: 140%;
    height: 130%;
    top: -12%;
    left: -20%;
  }
}

.PostInfo-header h3 {
  margin: 3rem 0;
  font-size: 3.5rem;
  font-family: "Just Another Hand", cursive;
}

.PostInfo-content {
  /* margin: 3rem; */
  /* line-height: 2.5rem; */
  /* font-family: "Montserrat", sans-serif; */
}

.PostInfo-text {
  /* margin: 1.5rem; */
  /* position: relative; */
}

.PostInfo-image {
  text-align: center;
}
.PostInfo-image img {
  box-shadow: 8px 8px 30px 30px #ddd;
  /* width: 100%;
  height: auto; */
}

.PostInfo-buttons {
  margin: 3rem;
  text-align: center;
}

.PostInfo-buttons button {
  margin: 0 2rem;
}

/* Image with overlay hover */

.PostInfo-image-container {
  position: relative;
  width: 100%;
  line-height: 2.5rem;
  font-family: "Montserrat", sans-serif;
  margin-bottom: 4rem;
}

.PostInfo-image img {
  display: block;
  width: 100%;
  max-height: 800px;
  min-height: 600px;
  height: auto;
}

.PostInfo-image-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 0.5s ease;
  background-color: #008cba;
}

.PostInfo-image-container:hover .PostInfo-image-overlay {
  opacity: 0.9;
}

.PostInfo-image-text {
  color: white;
  font-size: 1.2rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  width: 100%;
  padding: 1rem;
}

/* Responsive */

@media (max-width: 768px) {
  .PostInfo-image-container {
    line-height: 1.8rem;
    font-size: 1rem;
  }
  .PostInfo-image-text {
    font-size: 3vw;
    width: 100%;
  }
}
@media (max-width: 492px) {
  .PostInfo-image-container {
    line-height: 1.6rem;
    font-size: 1rem;
  }
  .PostInfo-image-text {
    font-size: 4vw;
    width: 100%;
    height: auto;
    transform: translate(-50%, -50%);
    text-align: center;
    overflow: auto;
  }
}

